@import url(https://fonts.googleapis.com/css2?family=David+Libre:wght@400;500;700&display=swap);
* {
    font-family: 'David Libre';
}

.sidebar {
    margin: 0;
    padding: 0;
    background-color: #3b1919;
    position: fixed;
    height: 100%;
    overflow: auto;
    z-index: 1;
    transition: 0.5s;
}

.sidebar h4 {
    text-align: left;
    padding: 15px 20px;
    color: #efefef;
}


.sidebar a {
    display: block;
    color: #efefef;
    padding: 16px;
    text-decoration: none;
    border-bottom: 1px solid rgb(105, 59, 59);
    font-size: 18px;
}

.sidebar a.active {
    background-color: rgb(105, 59, 59);
    color: #fff;
}

.sidebar a:hover:not(.active){
    background-color: rgb(105, 59, 59);
    color: #fff;
    text-decoration: none;
}

div.content {
    margin-left: 20px;
    padding: 1px 16px;
}

.toggler {
    position: absolute;
    left: 15px;
    top: 10px;
    border: none;
    border-radius: 5px;
    background-color: #fff;
    color: rgb(105, 59, 59);
    margin-bottom: 10px;
    font-size: 26px;
}

.close-btn {
    position: absolute;
    top: 5px;
    right: 10px;
    background-color: transparent;
    border: none;
    color: #efefef;
    font-size: larger;
    padding: 4px 8px;
}

.close-btn:hover{
    color: #c1c1c1;
}

@-webkit-keyframes loadingAnimation {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes loadingAnimation {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

.loading-label {
    padding: 3%;
    color: rgb(105, 59, 59);
}

.loading-label span{
    -webkit-animation: loadingAnimation 3s infinite;
            animation: loadingAnimation 3s infinite;
}

.content-collapse {
    max-height: 500px;
    overflow: hidden;
    transition: max-height .5s ease;
}

.content-close {
    max-height: 0;
}


